<template>
  <div class="">
    <components :is="d"></components>
  </div>
</template>

<script>
import {clientRect} from '@/common/js/base'

export default {
  data: ()=> ({
    d: 'pc',
  }),
  components: {
    page: ()=> import ('@/components/PageWrap/PageWrap.vue'),
    pc: ()=> import ('./pc.vue'),
    mobile: ()=> import ('./mobile.vue')
  },
  methods: {

  },
  created() {
    if(clientRect().w>500) {
      this.d = 'pc'
    }else {
      this.d = 'mobile'
    }
  }
}
</script>



